// 
class Status {
    basicMap: any
    constructor(basicMap: any = null) {
        this.basicMap = basicMap = new Map<string, string>()
        basicMap.set('', '-1')
        basicMap.set('', '0')
        basicMap.set('', '1')
        basicMap.set('', '4')
        basicMap.set('', '5')
        basicMap.set('', '6')
        basicMap.set('', '7')
        basicMap.set('', '8')
        basicMap.set('', '10')
    }

    getVal (key: string): string {
        let val: string = this.basicMap.get(key)
        return val
    }

    getKey (val: string): string {
        let key: string = ''
        this.basicMap.forEach((sKey, sVal) => {
            if(val == sKey) {
                key = sVal
            }
        })
        return key
    }
}

